.App { 
  display: grid;
  grid-template-areas: 
    "header header header"
    "nav article ads"
    "footer footer footer";
  grid-template-rows: 60px 1fr 60px;
  grid-template-columns: 20% 1fr 30%;
  grid-gap: 5px;
  height: 100vh;
  margin: 0;
  /* border: solid; */
  }

#pageHeader {
  grid-area: header;
  background: goldenrod;
}
#pageFooter {
  grid-area: footer;
  background: goldenrod;
}
#mainArticle { 
  grid-area: article;
  background: goldenrod;      
  }
#mainNav { 
  grid-area: nav; 
  background: goldenrod;
  }
#siteAds { 
  grid-area: ads; 
  background: goldenrod;
  }
header, footer, article, nav, div {
  padding: 20px;
  background: rgb(240, 220, 169);
  /* border: groove; */
}
